import { useEffect, useState } from "react";
import { Button } from "../ui/button";
import { Input } from "../ui/input";
import { useRouter } from "next/navigation";
import { Check, Star } from "lucide-react";
import { Textarea } from "../ui/textarea";

export const RatingForm = () => {
  const router = useRouter();

  const [userInfo, setUserInfo] = useState<{
    user: any;
    userId: string;
  } | null>(null);
  const [isSignedIn, setIsSignedIn] = useState<boolean>();

  const getSession = async () => {
    const session = await fetch(`/api/auth`).then((res) => res.json());
    setUserInfo(session);

    if (session) {
      setIsSignedIn(true);
    }
  };

  useEffect(() => {
    getSession();
  }, []);

  return (
    <div
      className="rounded-md bg-card px-6 py-10 font-geistsans"
      onClick={(e) => e.stopPropagation()}
    >
      <form
        className="space-y-3"
        onSubmit={async (e: React.FormEvent<HTMLFormElement>) => {
          e.preventDefault();
          const formData = new FormData(e.currentTarget);
          const content = formData.get("content") as string;

          if (isSignedIn) {
            await fetch(`/api/comments`, {
              method: "POST",
              headers: {
                "Content-Type": "application/json",
              },
              body: JSON.stringify({
                content,
                userId: userInfo?.userId,
              }),
            });

            router.refresh();
          } else {
            alert("sign in first");
          }
        }}
      >
        <div className="flex justify-between">
          {Array.from({ length: 5 }, (_, i) => (
            <Star className="my-2 size-7 opacity-20" key={i} />
          ))}
        </div>
        <Textarea name="content" type="text" placeholder="write a review" />
        <Button
          className="flex w-full items-center gap-1 text-center"
          type="submit"
        >
          Rate
          <Check className="border-2 border-white/0" />
        </Button>
      </form>
    </div>
  );
};
